@import url(https://fonts.googleapis.com/css?family=Roboto+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --SEC-COLOR: hsl(24, 100%, 50%);
  --DARK-TEXT: #2b2b2b;
  --PRI-COLOR: #f3f5f3;
}
body {
  text-align: center;
  font-family: "Roboto Slab";
}
input:focus {
  outline: none;
}
.item-mob {
  display: none;
}
.center-things {
  width: 100%;
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}

.dark-theme {
  background-color: #f3f5f3;
  background-color: var(--PRI-COLOR);
  color: #2b2b2b;
  color: var(--DARK-TEXT);
  height: 100%;
}
.navbar-container {
  display: grid;
  grid-template-columns: auto auto;
}
.app-title {
  text-align: center;
  margin-left: 10px;
  width: 35%;
}
.app-title p,
.app-title h1 {
  margin: 0;
}
.app-title p {
  color: hsl(24, 100%, 50%);
  color: var(--SEC-COLOR);
}
.app-title a {
  text-decoration: none;
  color: #2b2b2b;
  color: var(--DARK-TEXT);
}
.links {
  text-align: end;
  margin-top: 20px;
}
.links .link {
  color: var(---DARK-TEXT);
  padding: 15px;
  text-decoration: none;
}
.search-component {
  width: 100%;
}
.search-bar {
  margin: 10px;
  width: 70%;
  padding: 10px;
  border: 2px solid hsl(24, 100%, 50%);
  border: 2px solid var(--SEC-COLOR);
  background-color: #f3f5f3;
  background-color: var(--PRI-COLOR);
}
.search-btn {
  padding: 10px;
  background-color: hsl(24, 100%, 50%);
  background-color: var(--SEC-COLOR);
  border: 0;
  font-family: "Roboto Slab";
  font-weight: bold;
  cursor: pointer;
}
.go-back-btn {
  text-decoration: none;
  color: #2b2b2b;
  color: var(--DARK-TEXT);
  background-color: hsl(24, 100%, 50%);
  background-color: var(--SEC-COLOR);
  padding: 10px;
  margin: 20px;
  font-weight: bold;
  border: none;
}
.display-grid-wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 100px;
}
.item1 {
  float: left;
}
.item3 {
  margin-top: 30px !important;
  background-color: #2b2b2b;
  background-color: var(--DARK-TEXT);
}
.item5 {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  font-size: 150px;
}
.item4 {
  grid-column-start: 2;
  grid-column-end: 4;
  grid-row-start: 3;
}
.desc-val {
  font-size: 75px;
}
@media only screen and (max-width: 1100px) {
  .app-title {
    width: 100%;
  }
  .links {
    text-align: center;
  }
  .navbar-container {
    display: block;
    box-shadow: 1px 1px 5px rgb(141, 125, 125);
    padding: 10px;
  }
  .display-grid-wrapper {
    display: block;
  }
  .item1 {
    display: none;
  }
  .desc-val {
    font-size: 30px;
    margin: 0px;
  }
  .item5 {
    font-size: 75px;
    margin: 0 !important;
  }
  .item-mob {
    display: block;
  }
}

